<template>
	<div>
		<div class="store_content">
			<swiper></swiper>
			<ul>
				<router-link tag="li" v-for="item in obj[0]"   :to="/Details/+item.id" id="first_li" >
					<div><img :src="item.img" alt=""></div>
					<div>{{item.name}}</div>
					<div>{{item.money}}</div>
				</router-link>
			</ul>
			<div class="store_last">查看更多 ></div>
		</div>
	</div>
</template>
				
					
				
<script>
import swiper from "../components/swiper"
	export default{
	 	components:{
        	swiper,
      	},
		data(){
			return{
				obj:"",
			}
		},
		mounted(){
			this.$http.get("./../../static/Mall_Furnishings.json").then(
				function(res){
					this.obj=JSON.parse(res.bodyText)
				},function(){

				});
		}
	}
</script>
	
<style scoped>
		.store_content{width:15rem;height:.4rem}
		.store_content>ul{width:14.2rem;height:auto;padding:0 .4rem}
		.store_content>ul #first_li{width:6.84rem;height:9.16rem;margin-top:.28rem;display:inline-block}
		#first_li>div:nth-child(1){width:6.84rem;height:6.88rem;border-radius:.08rem}
		.store_content div img{width:100%;height:100%}
		#first_li>div:nth-child(2){width:6.84rem;height:.8rem;margin:.44rem 0 .2rem 0;font-size:.56rem;text-align:center;line-height:.8rem}
		#first_li>div:nth-child(3){width:6.84rem;height:.84rem;font-size:.6rem;text-align:center;line-height:.84rem;color:#fd888b}
		.store_last{text-align:center;width:100%;margin-top:.8rem;font-size:.56rem;color:#6a6a6a;margin-bottom:3.2rem}
		#first_li{margin-right:.38rem}
		#first_li:nth-of-type(2n){margin:0;}
</style>



