<script>


        window.onload=function(){
            /**
             * init传入参数依次是：正确密码(传空时不对比输入是否正确),密码键盘背景，标题，副标题
             * */
            PwdBox.init('000000','img/pwd_keyboard.png','请输入支付密码','安全支付环境，请放心使用！');
            /**
             *res格式：{status:'true或false',password:'用户输入的密码'}
             *
             */
            PwdBox.show(function(res){
                if(res.status){
                    //重置输入
                    alert('密码正确');
                    //关闭并重置密码输入
                    PwdBox.reset();
                }else{
                    alert('密码错误，您还有三次重试机会！');
                }
            });
        };
    </script>
