<template>
	<div>
		<div id="wrap">
			<swiper :options="swiperOption" ref="mySwiper" class="swiper-container">
			    <swiper-slide class="swiper-slide"><img src="../../static/img/page-1.png" alt=""></swiper-slide>
			    <swiper-slide class="swiper-slide"><img src="../../static/img/page-2.png" alt=""></swiper-slide>
			    <swiper-slide class="swiper-slide"><img src="../../static/img/page-3.png" alt=""></swiper-slide>
			    <swiper-slide class="swiper-slide"><img src="../../static/img/page-4.png" alt=""></swiper-slide>
			</swiper>
		    <div id="button">
				<router-link to="/Login" tag="div" class="sign_button inbutton" href="">登录</router-link>
				<router-link to="/registered" tag="div" class="sign_button upbutton" href="">注册</router-link>
		    </div>
		</div>
	</div>
</template>
			    
<script>
  // swiper options example:
  export default {
    name: 'carrousel',
    data() {
      return {
        swiperOption: {     
         
          autoplay:6000,         
       	  loop:true,
          onTransitionStart(swiper){
      
          },
        
        }
      }
    },
  
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
    },
    
  }
</script>
<style scoped>
		html,body {height: 100%;margin:0;padding:0;overflow:hidden;}
		#wrap {width: 100%;height: 100%;}
		.swiper-container {width: 100%;height: 100%;}
		.swiper-slide img {width: 100%;height: 100%;}
		#button {position:fixed; bottom: 3.76rem; z-index: 10;width: 100%;
		  padding: 0 1.84rem;font-size: 34px;text-align:center;line-height:1.64rem;}
		.sign_button {float:left;width:5.16rem;height:1.64rem;border-radius:0.16rem;font-size:0.68rem;}
		.inbutton {background: #65C4AA;margin-right: 0.92rem;}
		.upbutton {background: #fff;}
</style>
			  
			   
			
			
			    







