<template>
	<div>
		<div id="wrap">
			<div @click="goback" id="top_nav"><i class="iconfont icon-back1"></i></div>
			<div id="sign_head">
				<div>
					<i class="iconfont i_font"></i>
				</div>
				<p id="sign_title">登录</p>
			</div>
			<!-- 输入框 -->
			<div id="sign_inputbox">
				<div id="phone">
					<i class="iconfont icon-phone2">&nbsp</i>
					<span id="areacode">+86&nbsp</span>
					<i class="iconfont icon-select"></i>
					<input class="textbox phone_num" type="text" v-model="aa" placeholder="输入手机号">
					<hr>
				</div>
				

				<div id="password">
					<i class="iconfont icon-password"></i>
					<input class="textbox pass_num" type="text" placeholder="输入验证码">
					<div id="getcode">点击获取验证码</div>
					<hr>
				</div>

				<!-- 确认按钮 -->
				<div @click="JudgmentLogin" class="font_samestyle" id="sign_sureBtn" href="">确定</div>
				<div class="font_samestyle" id="signUp_agreement" href="">注册代表您同意用户协议</div>

			</div>
			<!-- 使用社交账号登录 -->
			<div id="others_signIn">
				
				<div id="footer_title">———————&nbsp;&nbsp;&nbsp;&nbsp;使用社交账号登录&nbsp;&nbsp;&nbsp;&nbsp;———————</div>
				
				<div id="others_account">
					<div class="iconstyle"><i class="iconfont iconfont_size"></i><br><span>微博</span></div>
					<div class="iconstyle"><i class="iconfont iconfont_size"></i><br><span>微信</span></div>
					<div class="iconstyle"><i class="iconfont iconfont_size"></i><br><span>QQ</span></div>
				</div>
				
			</div>
			
		</div>
	</div>
</template>
<script>
export default{
	 data(){
	    return{
	      aa:''
	    }
	  },
	  mounted(){
	    setInterval(function(){
	        
	    },2000);
	   
	  },

	methods:{
		goback(){
			this.$router.go(-1)
		},
		JudgmentLogin(){
			var aInput=document.getElementsByTagName('input');
			var te=/^1(3|4|5|7|8|)\d{9}$/g;
				if(te.test(aInput[0].value)){
					this.$router.push("/Store/secommend")
				}
				else{
					alert("输入的号码不正确！")
				}
		},
	}
}
</script>
<style scoped>
		html, body {height: 100%; } 
		#wrap {width:100%;height:100%;position: relative; } 
		#top_nav {position: absolute;top:0.6rem;padding-left: 0.36rem; } 
		.iconfont {font-size: 0.6rem;line-height: 0.6rem; } 
		#sign_head {position: absolute;top:2.48rem;padding:0 6.1rem;height:2.8rem; } 

		.i_font{width:2.8rem;height: 2.8rem;background:url(../../static/img/qqqq.png)no-repeat;display:inline-block;background-size:100%  100%; } 
		#sign_title {margin-top:0.64rem;font-size:0.68rem;font-weight:bold;text-align:center; }
		/*输入框 */
		#sign_inputbox {position: absolute;top:9.04rem;width:11.68rem;padding:0 1.78rem; } 
		.icon-phone2 {font-size:0.72rem;} 
		.icon-select {font-size: 0.32rem;} 
		.icon-password {font-size: 0.68rem;} 
		#areacode {font-size: 0.6rem;font-family:'PingFangSC-Regular'; } 
		.textbox {border-left: 0.02rem solid#979797;padding-left:0.18rem;font-size:0.6rem;line-height:0.92rem; } 
		.phone_num {margin-left:0.24rem;}
		.pass_num{margin-left:0.36rem;} 
		#phone {height:1.28rem;margin-bottom:0.48rem;} 
		#password {position:relative;height:1.28rem; } 
		#getcode{position:absolute;right:0;bottom:0.12rem;width:4.12rem;height:1.28rem;line-height:1.28rem;font-size:0.48rem;text-align:center;border:0.04rem solid #979797;border-radius:0.16rem;}
		hr{width: 10.2rem;float:right;margin:0.32rem 0 0; } 
		.font_samestyle {display:block;width:11.68rem;font-size:0.48rem;color: #8b8b8b; }
		#sign_sureBtn {height:1.64rem;margin-top:1.24rem;background:#65C4aa;border-radius:0.16rem;font-size: 0.6rem;
		color: #fff;text-align: center;line-height:1.64rem;}
		#signUp_agreement{text-align: right;margin-top:0.64rem;}
		#others_signIn {position: absolute;width: 100%;left:0;top:17.64rem; } 
		#footer_title {height:0.68rem;padding:0 1.68rem;font-size: 0.48rem;line-height:0.68rem;text-align:center;color:#8b8b8b;} 
		#others_account {width: 8.24rem;margin:0 auto;} 
		.iconstyle {float: left;margin-top:1.36rem;font-size:0.48rem;text-align:center; } 
		.iconfont_size { line-height:1.4rem; } 
		.iconstyle:nth-of-type(1) .iconfont{width:1.36rem;height:1.4rem;background:url(../../static/img/WB_logo.png)no-repeat;background-size:100% 100%;display:inline-block;}
		.iconstyle:nth-of-type(2) .iconfont {margin:0 2.0rem;width:1.4rem;height:1.26rem;background:url(../../static/img/weix-logo.png)no-repeat;background-size:100% 100%;display:inline-block; } 
		.iconstyle:nth-of-type(3) .iconfont{width:1.4rem;height:1.26rem;background:url(../../static/img/qq.png)no-repeat;background-size:100% 100%;display:inline-block;}
		.iconstyle span {display: block;margin-top:0.48rem;}
		html,body{height:100%;} 

</style>


