<template>
  <div>
    <div class="store_top">
      <Recommend></Recommend>
      <storechoose></storechoose>
    </div>
    <div class="recommend_content">
      <transition :name="transitionName">
        <router-view></router-view>
      </transition>
    </div>
    <store-footer></store-footer>
  </div>
</template>

<script>
  import Recommend  from  '../components/Recommend'
  import Storechoose  from '../components/Storechoose'
  import storeFooter from '../components/storefooter'

 

  export default{
    components:{
      Recommend,Storechoose,storeFooter
    },
    mounted(){
      
    },
    data () {
      return {
        // transitionName: 'slide'
      }
    },
    watch: {
      '$route'(to, from) {
        // const to = to.path.split('/').length
        // const from = from.path.split('/').length
          console.log(from.path)
          console.log(to.path)
        this.transitionName = to < from ? 'slide_back' : 'slide'
      }
    },
  }

</script>
<style scoped>
.store_top{width:15rem;height:3.6rem;background-color:#fff}
      .slide-enter{
        position: absolute;
        width: 100%;
      }
      .slide-leave{
        position: absolute;
        width: 100%;
      }
      .slide-enter-active{
        transition: all 0.3s linear;
      }
      .slide-leave-active {
        position: absolute;
        transition: all 0.3s linear;
        transform: translate(-100%);
      }
      .slide-enter{
        transform: translateX(100%);
      }
     
     

</style>


