<template>
	<div>

		<div id='ShopingCar'>
			<!-- 我的购物车======头部部分 -->
			<shoppingcartnav></shoppingcartnav>
			<!-- 我的购物车======内容部分 -->
		<div id='ShopingCar-contnt'>
				<div id='mug'>
					<div class='mug-choose'>
						<!-- 被选中物品的按钮 -->
						<div id='mug-choose-yes'></div>
					</div>
					<div id='mug-photo'></div>
					<div id='mug-interdius'>
						<p id='mugname'>Line高白玉骨瓷马克杯</p>
						<p id='mugcolor'>颜色：哑光绿</p>
						<p class='mugtianjia'>
							<b id='mugtianjia-minus'>-</b>
							<b id='mugtianjia-one'>1</b>
							<b id='mugtianjia-add'>+</b>
						</p>
						<img class='mug-dustbin' src="../../static/img/dustbin.jpg" alt="">
						<span id='mugprice'>￥169</span>
					</div>
				</div>
				<div id='mug'>
					<div class='mug-choose'>
						<div id='mug-choose-yes'></div>
					</div>
					<div id='mug-photo'></div>
					<div id='mug-interdius'>
						<p id='mugname'>Line高白玉骨瓷马克杯</p>
						<p id='mugcolor'>颜色：哑光绿</p>
						<p class='mugtianjia'>
							<b id='mugtianjia-minus'>-</b>
							<b id='mugtianjia-one'>1</b>
							<b id='mugtianjia-add'>+</b>
						</p>
						<img class='mug-dustbin' src="../../static/img/dustbin.jpg" alt="">
						<span id='mugprice'>￥169</span>
					</div>
				</div>
			</div>
			<!-- 我的购物车======尾部部分 -->
			<div id='ShopingCar-foot'>
				<div id='balance'>
					<div id='balance-choose'>
						<!-- 被选中物品的按钮 -->
						<div class='balance-choose-yes'></div>
					</div>
					<span class='quanxuan'>全选</span>
					<span class='total'>总计（不含运费）：
						<span>￥</span>
						<span>0</span>
					</span>
					<div id='xiadan'>立即下单</div>
				</div>
			</div>
		</div>  
		<!-- 删除页面 -->
		<div class='ShopingCar'>
			<div class='deleteshop'>
				<span>确定要删除这个商品吗？</span>
				<span>取消</span>
				<span>确定</span>
			</div>
		</div>
		<storefooter></storefooter>
	</div>
</template>
<script>
	import storefooter  from  '../components/storefooter'
	import shoppingcartnav from '../components/shoppingcartnav'

  	export default{
	   components:{
	      storefooter,shoppingcartnav
	   },
	   mounted(){
	   		var aMugdustbin=document.getElementsByClassName('mug-dustbin');
	   		var oShopingCar=document.getElementsByClassName('ShopingCar')[0];
	   		var oDeleteshop=document.getElementsByClassName('deleteshop')[0];
	   		var aSpan=oDeleteshop.getElementsByTagName('span');
	   		var aMugchoose=document.querySelectorAll(".mug-choose");
	   		var aMugchooseyes=document.querySelectorAll("#mug-choose-yes");

	   		var oBalancechoose=document.querySelector("#balance-choose");
	   		var oBalancechooseyes=document.querySelector(".balance-choose-yes");
	   		for(var i=0;i<2;i++){
	   			
	   			aMugdustbin[i].addEventListener('touchstart',fnn1);
	  			aMugchoose[i].index=i
	   			aMugchoose[i].addEventListener("touchstart",fnn3);
	   			oBalancechoose.addEventListener("touchstart",fnn4);
	   		}
	   		aSpan[1].addEventListener('touchstart',fnn2);
	   		function fnn1(){
	   			oShopingCar.style.display="block";
	   		};
	   		function fnn2(){
	   			oShopingCar.style.display="none";	
	  		};
	  		function fnn3(){
	  			if(aMugchooseyes[this.index].style.display=="block")
	  			{
	  				aMugchooseyes[this.index].style.display="none";
	  			}
	  			else{

	  				aMugchooseyes[this.index].style.display="block";
	  			}
	  		};
	  		function fnn4(){
	  			if(oBalancechooseyes.style.display=="none")
	  			{
		  			oBalancechooseyes.style.display="block";
		  			for(var i=0;i<2;i++){
		  				aMugchooseyes[i].style.display="block";
		  			}
	  			}
	  			else{
	  				oBalancechooseyes.style.display="none";
		  			for(var i=0;i<2;i++){
		  				aMugchooseyes[i].style.display="none";
		  			}
	  			}

	  		}


	   			
	   			
	   	}
  	}
</script>
<style scoped>
		#ShopingCar{width:15rem;background:#f9f9f9;overflow:hidden}
		#ShopingCar-contnt{width:15rem;height:11.58rem}
		#mug{width:100%;height:5.7rem;background:#fff;box-shadow:0 -.06rem .06rem #fafafa;margin-bottom:.16rem}
		.mug-choose{display:inline-block;width:.64rem;height:.64rem;border-radius:50%;border:.04rem solid #8b8b8b;position:relative;left:0;top:2.46rem}
		#mug-choose-yes{width:.4rem;height:.4rem;background:#65c4aa;border-radius:50%;margin:.12rem;display:none}
		#mug-photo{width:3.08rem;height:3.12rem;border-radius:.2rem;background:url(../../static/img/ware.png)no-repeat;background-size:100% 100%;box-shadow:0 0 .2rem #ececec;position:relative;top:.48rem;left:1.02rem}
		#mug-interdius{width:9.06rem;height:5.7rem;position:relative;top:-3.9rem;left:5.28rem;line-height:1.92rem;font-family:PingFangSC-Regular;font-size:.6rem;color:#404040}
		#mugname{position:relative;top:.8rem;line-height:.84rem;font-size:.6rem;color:#404040;letter-spacing:-.02rem;font-family:PingFangSC-Regular}
		#mugcolor{position:relative;top:.52rem;font-size:.48rem;font-family:PingFangSC-Regular;color:#6A6A6A;letter-spacing:-.01rem;text-indent:.1rem}
		.mugtianjia{position:relative;top:.6rem;width:3.9rem;height:1.24rem;border:.04rem solid #8b8b8b;border-radius:.12rem}

		.mugtianjia>b{display:inline-block;width:1.24rem;height:1.24rem;float:left;font-size:.68rem;color:#404040;letter-spacing:-.02rem;font-family:PingFangSC-Regular;text-align:center;line-height:1.24rem}
		.mugtianjia>b:nth-of-type(1){border-radius:.08rem 0 0 .08rem;border-right:.04rem solid #979797}
		.mugtianjia>b:nth-of-type(2){border-right:.04rem solid #979797}
		.mugtianjia>b:nth-of-type(3){border-radius:.08rem 0 0 .08rem}
		.mug-dustbin{position:absolute;top:1.48rem;right:0;}
		#mugprice{position:relative;top:-.8rem;left:6rem;display:inline-block;font-family:PingFangSC-Regular;font-size:.68rem;color:#fd888b;letter-spacing:-.02rem}
		#ShopingCar-foot{width:15rem;height:3.92rem;margin-top:7.24rem}
		#balance{position:relative;width:13.66rem;height:1.92rem;margin:0 auto}
		#balance-choose{display:inline-block;width:.64rem;height:.64rem;border-radius:50%;border:.04rem solid #8b8b8b;margin-top:.64rem}
		.balance-choose-yes{display:inline-block;width:.4rem;height:.4rem;border-radius:50%;margin:.12rem;background:#65c4aa;display:none}
		.quanxuan{position:relative;top:-.14rem;left:.2rem;font-size:.68rem;color:#404040;letter-spacing:-.02rem;font-family:PingFangSC-Regular}
		.total{position:relative;left:1rem;font-size:.56rem;top:-.14rem;color:#404040;letter-spacing:-.02rem}
		.total>span{display:inline-block;color:#fd888b;font-size:.48rem}
		.total>span:nth-of-type(1){margin-left:-.2rem}
		.total>span:nth-of-type(2){margin-left:.04rem}
		#xiadan{width:3.2rem;height:1.6rem;border-radius:.12rem;background:#65c4aa;float:right;margin-top:.2rem;text-align:center;line-height:1.6rem;font-family:PingFangSC-Regular;font-size:.56rem;color:#FFF;letter-spacing:.02rem}
		.ShopingCar{position:absolute;top:0;width:15rem;height:25.92rem;z-index:10;margin:0 auto;background-color:rgba(10,10,10,.7);display:none}
		.deleteshop{width:8.6rem;height:4.6rem;border-radius:.12rem;background:#fff;margin:0 auto;margin-top:8rem}
		.deleteshop>span{display:inline-block;font-family:PingFangSC-Regular;font-size:.68rem;letter-spacing:-.02rem;text-align:center}
		.deleteshop>span:nth-of-type(1){top:.4rem;width:8.6rem;height:2.08rem;border-bottom:.04rem solid #f2f2f2;line-height:2.08rem;margin:0 auto;color:#404040;text-indent:.7rem}
		.deleteshop>span:nth-of-type(2){width:4.2rem;height:2.08rem;border-radius:.12rem;border-right:.04rem solid #f2f2f2;line-height:2.08rem;}
		.deleteshop>span:nth-of-type(3){height:2.08rem;border-radius:.12rem;line-height:2.08rem;color:#FD888B;text-indent:1.08rem;}

</style>


