<template>
	<div>
			<div class="community-recommendation-page">
				<div class="solicitation">
					<div class="solicitation-text">
						<img id="solicitation-text-img" src="../../static/img/weibt-4.png"/>
						
						<span>正在征集</span>
					</div>
					<div class="viewport-solicitation">
						<ul class="solicitation-img">
							<li v-for="item in obj[0]">
								<img :src="item.img1"/>
								<div class="solicitation-img-div">
									<span>{{item.font}}</span>
									<img :src="item.img2"/>
									<a>{{item.comment}}</a>
									<img :src="item.img3"/>
								</div>
							</li>
						</ul>
					</div>
				</div>
			</div>
				
			<div class="commounity-forum" v-for="item in obj[1]">
				<div class="head-portrait">
					<img :src="item.img1"/>
					<span>{{item.title}}</span>
				</div>
				<div class="products-recommendation">
						<ul>
							<li>
								<img :src="item.img2"/>
							</li>
							<li>
								<img :src="item.img3"/>
							</li>
							<li>
								<img :src="item.img4"/>
							</li>
						</ul>
				</div>
				<span>1/3</span>
				<div class="dianzhan">
					<i><img :src="item.img5"/>
						<a>{{item.number1}}</a>
					</i>
					<i><img :src="item.img6"/>
						<a>{{item.number2}}</a>
					</i>
					<i><img :src="item.img7"/>
						<a>{{item.number3}}</a>
					</i>
				</div><br />
				<div class="cont-text">
					<span>{{item.font}}</span>
				</div>
			</div>
	</div>
</template>
<script>
export default{
	data(){
		return{
			obj:"",
		}
	},
	mounted(){
		this.$http.get("./../../static/community.json").then(
			function(res){
				this.obj=JSON.parse(res.bodyText);
			},function(){

			})
	}
}
</script>
<style scoped>
			.community-recommendation-page{width: 15rem;position: relative;top: 0.2rem;}
			.solicitation{width: 15rem;height: 5rem;background: white;}
			.solicitation-text{width: 15rem;height: 1rem;line-height: 1rem;}
			.solicitation-text>img{width:0.68rem ;height: 0.72rem;margin-left: 0.72rem;margin-top: 0.16rem;}/*margin-left: 0.72rem;margin-top: 0.16rem;*/
			.solicitation-text>span{color: #65c4aa;font-size:0.55rem ;position: absolute;left: 1.7rem;top: 0.13rem;}
			.viewport-solicitation{width: 15rem;height: 4rem;font-size:0 ;overflow: hidden;overflow-x: scroll;}
			.solicitation-img{width: 150rem;}
			.solicitation-img>li{width: 6.6rem;height: 3.48rem;border-radius:0.2rem ;border: 1px solid gainsboro;float: left;margin-left: 0.2rem;}
			/*.solicitation-img>li:first-child{margin-left: 0.68rem;}*/
			.solicitation-img>li:last-child{margin-right: 0.68rem;}
			.solicitation-img>li>img{width: 100%;height: 100%;border-radius:0.2rem ;}
			.solicitation-img>li>div{width: 5.08rem;height: 1.64rem;background-color: rgba(255,255,255,0.6);position: relative;top: -2.5rem;left: 0.8rem;text-align: center;line-height: 0.8rem;}
			.solicitation-img>li>div>span{font-size: 0.4rem;color:#404040;position: relative;top: 0;}
			.solicitation-img>li>div>a{width: 1.65rem;display: inline-block;height: 0.42rem;font-size: 0.4rem;color:#848484;line-height: 0.52rem;z-index: 1000;position: relative;}
			.solicitation-img>li>div>img{width: 0.6rem;height: 0.04rem;position: relative;top: -0.15rem;}
			
			.commounity-forum{width: 15rem;height: 19.2rem;position: relative;background-color: white;top: 0;}
			.head-portrait{width: 15rem;height: 2rem;background-color: white;position: absolute;left: 0;top: 0;}
			.head-portrait>img{width: 1.2rem;height: 1.2rem;border-radius: 50%;margin-top: 0.4rem;margin-left: 0.62rem;}
			.head-portrait>span{color: #65c4aa;font-size: 0.6rem;position: absolute;top: 0.5rem;left: 2.4rem;}
			.products-recommendation{width: 13.6rem;height: 12rem;overflow: hidden;overflow-x: scroll;position: relative;margin: auto;border-radius:0.2rem ;top: 2rem;}
			.products-recommendation>ul{width: 50rem;font-size:0 ;}
			.products-recommendation>ul>li{width: 13.6rem;float: left;height: 12rem;margin-right: 0.2rem;}
			.products-recommendation>ul>li>img{width: 13.6rem;height: 12rem;}
			.commounity-forum>span{font-size: 0.4rem; display: inline-block;width: 1.28rem;height: 1.28rem;background-color: white;border-radius: 50%;position: relative;text-align: center;line-height: 1.28rem;top: 0rem;left: 12.1rem;}
			.dianzhan{width: 15rem;height: 0.8rem;position: relative;	top: 1.0rem;background-color: white;}
			.dianzhan>i{font-style: normal;margin-left: 0.8rem;}
			.dianzhan>i:nth-child(1)>img{width: 0.62rem;height: 0.62rem;}
			.dianzhan>i:nth-child(2)>img{width: 0.8rem;height: 0.62rem;}
			.dianzhan>i:nth-child(3)>img{width: 0.62rem;height: 0.62rem;}
			.dianzhan>i>a{font-size: 0.65rem;color: #404040;margin: 0 0.4rem;}
			.cont-text>span{overflow: hidden;white-space: nowrap;text-overflow: ellipsis;position: relative;top: .4rem;font-size: 0.6rem;color: #404040;display: inline-block;width:12.2rem ;left: 0.7rem;}
		
			

</style>


