			.details-footer>ul{position: relative;top: 1.5rem;}
			.details-footer>ul>li{list-style-type:disc ;margin-left: 1.62rem;width: 12rem;}
			.details-footer>ul>li>h6{font-size: 0.62rem;color: #404040;}
			.details-footer>ul>li>p{color: #7a7a7a;font-size: 0.46rem;line-height: 0.9rem;}
			.lowest-level{width: 6.4rem;height: 0.8rem;line-height: 0.8rem;text-align: center;position: relative;left: calc(50% - 3.2rem);top: 3rem;}
			.lowest-level>p{text-transform: capitalize;font-size: 0.4rem;position: relative;top: -0.69rem;background-color: white;z-index: 10;width: 3rem;left: calc(50% - 1.5rem);}
		
			/*选择规格各数量模块*/		
			.specifications-choice{width: 15rem;height: 13.2rem;position: absolute;top: 16.82rem;clear: both;display: none;}
			.specifications-choice>li>i{display: inline-block;width: 0.6rem;height: 0.6rem;position: absolute;top: 0.17rem;}
			.specifications-choice>li>i>img{width: 0.6rem;height: 0.6rem;}
			.choice-numbler{width: 15rem;height: 5rem;background-color: white;}
			/*.choice-numbler>i{display: inline-block;border: ;}*/
			.choice-numbler>img{width:3.5rem ;height: 3.5rem;border-radius: 0.4rem;margin-top: 0.8rem;margin-left: 0.8rem;box-shadow:0 0 10px #cCC ;-moz-box-shadow:0 0 10px #cCC ;-webkit-box-shadow:0 0 10px #cCC ;float: left;}
			.choice-numbler-text{width:10rem; height: 3.4rem;position: absolute;right: 0;top: 2.65rem;font-size: 0.52rem;line-height: 0.9rem;}
			.choice-numbler-text>span{color: red;}
			.choice-numbler-text>p{color: #878787;}
			.choice-numbler-text>div{color: #414141;width:3.8rem ;height: 1.24rem;border: 0.04rem solid #8b8b8b;border-radius: 0.2rem;font-size: 0;margin-top: 0.3rem;}
			.choice-numbler-text>div input{width: 1.24rem;height: 1.24rem;font-size: 0.6rem;}
			.choice-numbler-text>div>a{display: inline-block;width:1.24rem ;height: 1.24rem;border:0.04rem solid #8B8B8B;border-bottom: none;border-top: none;text-align: center;line-height: 1.24rem;font-size: 0.6rem;}
			.specifications-color{width: 15rem;height: 6rem;background-color: white;position: absolute;top: 6.5rem;}
			.specifications-color>h3{font-size: 0.6rem;color: #707070;margin-left: 0.9rem;margin-bottom: 0.2rem;}
			.specifications-color>li{font-size: 0.6rem ;color: #404040; float: left;border: 0.04rem solid #8B8B8B;border-radius:0.2rem ;text-align: center;line-height: 1.6rem;height: 1.6rem;width: 4rem;margin-left:0.72rem ;margin-top: 0.44rem;}
		
			/*分享模块*/
			.details-share{width: 15rem;height:12.4rem ;background-color: #cccccc;position: fixed;bottom: 0;z-index: 2000;display: none;}
			.details-share>button{font-size: 0.56rem;color: #404040;position: absolute;right: 0.6rem;top: 0.55rem;background: transparent;border: none;outline: none;}
			.details-share>h3{text-align: center;height: 0.64rem;width: 15rem;color: #444444;font-size: 0.64rem;top:1.28rem ;position: absolute;}
			.details-share>p{width:10.8rem ;position: absolute;left: calc(50% - 5.4rem);color: #404040;font-size: 0.59rem;top: 3.32rem;line-height: 1rem;height: 4.16rem;border-bottom: 0.04rem solid #bebebe;}
			.share-contact{width: 10.8rem;height:3.8rem ;position: absolute;top:7.98rem ;text-align: center;font-size: 0;left: calc(50% - 5.4rem);}
			.share-contact>li{width: 2.4rem;height: 3.4rem;float: left;margin: 0 0.46rem;}
			.share-contact>li:nth-child(1){margin-left: 0.92rem;}
			.share-contact>li>i{width: 2.4rem;height: 2.4rem;display: inline-block;border-radius: 50%;-moz-border-radius: 50%;}
			.contact:nth-child(1)>i{border: 0.04rem solid red;}
			.contact:nth-child(2)>i{border: 0.04rem solid #5c94d0;}
			.contact:nth-child(3)>i{border: 0.04rem solid #42ae3c}
			.contact>p{text-align: center;width: 2.4rem;height:1rem ;line-height: 1rem;font-size: 0.44rem;color: #5a5a5a;}
			.contact:nth-child(1)>i>img{width: 1.48rem;height: 1.16rem;margin: auto;margin-top: 0.6rem;}
			.contact:nth-child(2)>i>img{width: 1.36rem;height: 1.36rem;margin: auto;margin-top: 0.48rem;}
			.contact:nth-child(3)>i>img{width: 1.74rem;height: 1.44rem;margin: auto;margin-top: 0.48rem;}

	
</style>


