<template>
  <div>
    <ul class="store_footer">
       <router-link to="/Store" tag="li"><i></i>商城 </router-link>
       <router-link  to="/community" tag="li"><i></i>社区 </router-link>
       <router-link to="/shoppingCart" tag="li"><i></i>购物车 </router-link">
       <router-link to="/myhome" tag="li"><i></i>我的 </router-link>
    </ul>
  </div>
</template>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<script>
import store1 from "../../static/img/store2.png"
import photo2 from "../../static/img/photo2.png"
import shop2 from "../../static/img/shop2.png"
import mine2 from "../../static/img/mine2.png"
export default{
  data(){
    return{

      store1:'../../static/img/store2.png',
      photo2:'../../static/img/photo2.png',
      shop2:'../../static/img/shop2.png',
      mine2:'../../static/img/mine2.png'
    }
  },
   

  mounted(){
    var  oStorefooter=document.querySelector(".store_footer");
    var aI=oStorefooter.querySelectorAll('i');
 
    if(this.$route.fullPath.indexOf('Store')>0)
        {
          aI[0].style.background="url("+store1+")no-repeat";
           aI[0].style.backgroundSize="100% 100%";
        }
     if(this.$route.fullPath.indexOf('community')>0)
        {
          aI[1].style.background="url("+photo2+")no-repeat";
           aI[1].style.backgroundSize="100% 100%";
        }
     if(this.$route.fullPath.indexOf('shoppingCart')>0)
        {
          aI[2].style.background="url("+shop2+")no-repeat";
           aI[2].style.backgroundSize="100% 100%";
        }
    if(this.$route.fullPath.indexOf('myhome')>0)
        {
          aI[3].style.background="url("+mine2+")no-repeat";
           aI[3].style.backgroundSize="100% 100%";
        }
  }
}
   
</script>
<style scoped>
    .store_footer{width:calc(88%);height:1.96rem;position:fixed;bottom:0;display:flex;justify-content:space-between;background-color:#fff;padding:0 1rem}
    .store_footer>li{width:2.48rem;height:2rem;font-size:.4rem;text-align:center}
    .store_footer>li>i{display:block;margin:0 auto;width:0.92rem;height:0.88rem;}
    .store_footer>li:nth-of-type(1) i{background:url(../../static/img/store1.png)no-repeat;background-size:100% 100%;}
    .store_footer>li:nth-of-type(2) i{background:url(../../static/img/community1.png)no-repeat;background-size:100% 100%;}
    .store_footer>li:nth-of-type(3) i{background:url(../../static/img/shop1.png)no-repeat;background-size:100% 100%;}
    .store_footer>li:nth-of-type(4) i{background:url(../../static/img/my1.png)no-repeat;background-size:100% 100%;}
    .on{color:#65c4aa}
</style>



