import Vue from 'vue'
import Router from 'vue-router'





// 启动页
import Landing  from '../Landinginterface/Landing'
import Login from '../Landinginterface/Login'
import registered from '../Landinginterface/registered'
import Othershomepage from '../Landinginterface/Othershomepage'//ta人主页
import Itscollection from '../Landinginterface/Itscollection'//ta的收藏

// 商城
import Store from '../pages/Store'

import Secommend from '../Mallsubpage/secommend'
import Furnishings from '../Mallsubpage/Furnishings'
import Incorporated from '../Mallsubpage/Incorporated'
import Kitchenware from '../Mallsubpage/Kitchenware'
import homeTextiles from '../Mallsubpage/homeTextiles'

import Danishlife from '../Subroutine/Danishlife'  //丹麦生活








//社区
import Community from '../pages/Community'
import Comrecommend from '../Communitysubpage/Comrecommend'
import Comuptodate from '../Communitysubpage/Comuptodate'
import Comfurnishings from '../Communitysubpage/Comfurnishings'
import Comincorporated from '../Communitysubpage/Comincorporated'
import Comkitchenware from '../Communitysubpage/Comkitchenware'
import Comhometextiles from '../Communitysubpage/Comhometextiles'

import Details from '../Communitysubpage/Details'

// 购物车
import ShoppingCart from '../pages/ShoppingCart'


// 我的
import Myhome from '../pages/Myhome'
import mycollection from '../Subroutine/mycollection'

import Orderl from '../Ordertotal/Orderl'  //订单
import Orderall from '../Ordersubpage/Orderall'
import Orderpayment from '../Ordersubpage/Orderpayment'
import Ordertobeshipped from '../Ordersubpage/Ordertobeshipped'
import Orderhasbeenshipped from '../Ordersubpage/Orderhasbeenshipped'
import Orderhasbeencompleted from '../Ordersubpage/Orderhasbeencompleted'
import seedetails from '../Ordersubpage/seedetails'

import myinformation from '../Subroutine/myinformation'
import Service from '../Subroutine/Service' //客服

import setup from '../Subroutine/setup'  //设置


//搜索
import Seathepage from '../searchfor/Seathepage'
import Seatotal from '../searchfor/Seatotal'

import SearchSingleP from '../seaSubroutine/SearchSingleP'
import Pagepost from '../seaSubroutine/Pagepost'
import Pageuser from '../seaSubroutine/Pageuser'

//支付



Vue.use(Router)

export default new Router({
	linkActiveClass:'on',
  routes: [
    {
      path: '/',
      redirect:'/Landing',
    },
    {
      path:'*',
      redirect:'/Landing',
    },
    // 启动页
    {
      path: '/Landing',
      component:Landing,
    },
    //登录
    {
      path:'/Login',
      component:Login,
    },
    {
      path:'/registered',
      component:registered,
    },
    //ta人主页
    {
      path:'/Othershomepage',
      component:Othershomepage,
    },
    //ta的收藏
    {
      path:'/Itscollection',
      component:Itscollection,
    },
    //商城
    {
      path: '/Store',
      component: Store,
      redirect:'/Store/secommend',
      children:[
        {path:'',component:Secommend},
        {path:'Secommend',component:Secommend},
        {path:'Furnishings',component:Furnishings},
        {path:'Incorporated',component:Incorporated},
        {path:'Kitchenware',component:Kitchenware},
        {path:'homeTextiles',component:homeTextiles},
      ]
    },
    //丹麦生活
    {
      path:'/Danishlife',
      component:Danishlife,
    },
    // 社区
    {
      path: '/Community',
      component: Community,
      redirect:'/Community/Comrecommend',
      children:[
        {path:'',component:Comrecommend},
        {path:'Comrecommend',component:Comrecommend},
        {path:'Comuptodate',component:Comuptodate},
        {path:'Comfurnishings',component:Comfurnishings},
        {path:'Comincorporated',component:Comincorporated},
        {path:'Comkitchenware',component:Comkitchenware},
        {path:'Comhometextiles',component:Comhometextiles},
      ]
    },
    //详情s
    {
      path:'/Details/:id',
      component:Details,

    },
    //订单详情
    {
      path:'/seedetails',
      component:seedetails,
    },
    // 购物车
     {
      path: '/ShoppingCart',
      component: ShoppingCart,
    },
    // 我的
    {
      path: '/Myhome',
      component: Myhome,
    },
    //我的信息
    {
      path:'/Myhome/myinformation',
      component:myinformation,
    },
    {
      path:"/Myhome/setup",
      component:setup,
    },
    //客服
    {
      path:'/Myhome/Service',
      component:Service,
    },
    //订单
    {
      path:'/Myhome/Orderl',
      component:Orderl,
      // redirect:'/Myhome/Orderl/Orderall',
      children:[
        {path:'',component:Orderall},
        {path:'Orderall',component:Orderall},
        {path:'Orderpayment',component:Orderpayment},
        {path:'Ordertobeshipped',component:Ordertobeshipped},
        {path:'Orderhasbeenshipped',component:Orderhasbeenshipped},
        {path:'Orderhasbeencompleted',component:Orderhasbeencompleted},
        {path:'seedetails',component:seedetails},
      ]
    },
    //我的收藏
    {
      path:'/Myhome/mycollection',
      component:mycollection,
    },
    //搜索
    {
      path:'/Seathepage',
      component:Seathepage,
    },
    //搜索总
    {
      path:'/Seatotal',
      component:Seatotal,
      redirect:'/Seatotal/SearchSingleP',
      children:[
        {path:'',component:SearchSingleP},
        {path:'/SearchSingleP',component:SearchSingleP},
        {path:'/Pagepost',component:Pagepost},
        {path:'/Pageuser',component:Pageuser},
      ],
    },
  
  ]
})



